import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import ButtonTransparent from './ButtonTransparent';
import { HorizontalLine } from './HorizontalLine';

import styles from './styles.scss';

const cd = cssDecorator(styles);

export interface ButtonResetProps {
  /** Колбэк при клике */
  onClick: VoidFunction;

  /** Флаг видимости кнопки */
  isVisible?: boolean;

  /** Текст кнопки */
  label?: string;

  /** Data атрибут для тестирования */
  dataTestId?: string;

  /** Data атрибут для тестирования кнопки */
  buttonDataTestId?: string;
}

const ButtonReset = ({
  onClick,
  dataTestId,
  buttonDataTestId,
  label = 'Сбросить',
  isVisible = true
}: ButtonResetProps): React.ReactElement => (
  <div
    className={ cd('block_button_reset', { hidden: !isVisible }) }
    data-test-id={ dataTestId }
  >
    <ButtonTransparent
      onClick={ onClick }
      dataTestId={ buttonDataTestId }
      label={ label }
    />
    <HorizontalLine />
  </div>
);

export default ButtonReset;
